<template>
  <div id="root">
      <SoundButton v-for="character in getCharacters" v-bind:key="character.name" class="sound-button"
                   v-bind:audioPlayer="audioPlayer"
                   v-bind:character-file-name="character.fileName"
                   v-bind:character-name="character.name"
                   v-bind:sound-file-name="character.soundFileName"/>
  </div>
</template>

<script>
import SoundButton from '@/components/SoundButton'

export default {
  name: 'SoundBtnsPanel',
  components: {SoundButton},
  data () {
    return {
      audioPlayer: new Audio()
    }
  },
  computed: {
    getCharacters: function () {
      return this.$store.state.characters.characters
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  h1, h2 {
    font-weight: normal;
  }

  ul {
    list-style-type: none;
    padding: 0;
  }

  li {
    display: inline-block;
    margin: 0 10px;
  }

  a {
    color: #42b983;
  }

  .sound-button  {
    margin: 1%;
  }

  #root {
    padding: 2% 0;
  }
</style>



