import Vue from 'vue'
import Router from 'vue-router'
import SoundBtnsPanel from '@/components/SoundBtnsPanel'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'SoundBtnsPanel',
      component: SoundBtnsPanel
    }
  ]
})



