<template>
  <img ref="characterImage" v-on:click="playQuote" v-bind:src="getImage" v-bind:alt="$props.characterName"/>
</template>

<script>
import JQuery from 'jquery'
let $ = JQuery

export default {
  props: ['audioPlayer', 'characterFileName', 'characterName', 'soundFileName'],
  name: 'SoundButton',
  data () {
    return {
      series: 'fc-de-kampioenen'
    }
  },
  methods: {
    playQuote: function () {
      const audioPlayer = this.$props.audioPlayer
      const image = this.$refs.characterImage
      const src = require('../assets/fc-de-kampioenen/sound/' + this.$props.soundFileName + '.mp3')

      if (audioPlayer.paused) {
        audioPlayer.src = src
        audioPlayer.play()
        playGlowingAnimation()
      }

      function playGlowingAnimation () {
        if (!audioPlayer.paused && isSameSource()) {
          glowImage()
        } else {
          $(image).show()
        }
      }

      function glowImage () {
        $(image).fadeIn(700, function () {
          $(image).fadeOut(700, playGlowingAnimation)
        })
      }

      function isSameSource () {
        const audioSrc = audioPlayer.src.split('/').pop()
        const originalSrc = src.split('/').pop()
        return audioSrc === originalSrc
      }
    }
  },
  computed: {
    getImage: function () {
      return require('../assets/' + this.$data.series.toString() + '/' + this.$props.characterFileName.toString())
    }
  }
}
</script>

<style scoped>
  img {
    border-radius: 15px;
    border: 1px solid black;
  }
</style>



